body{
  background: #bebebe;
}
.App {
  text-align: center;
}
.App-Header, .App-Footer {
  background-color: #222;
  padding: 10px;
  color: white;
}
.App-Table{
  width: 80%;
  margin: 10px auto 10px auto;
}
.image{
  width: 35px;
  height: 35px;
  border-radius: 50%;
  border:2px solid black;
  margin: auto;
}
table a{
  color:white;
}
.Buttons{
  font-family: 'Raleway';
  margin-top:10px;
}
.Buttons button{
  margin-left: 5px;
  margin-right: 5px;
  cursor: pointer;
  background: #222;
  border-radius: 0;
}



